import { Component, Prop } from '@stencil/core';
import { DataLayer } from '../../global/dataLayer';

@Component({tag: 'ks-tracker-order'})
export class TrackerOrder {

    @Prop() checkout: boolean = false;
    @Prop() form: boolean = false;
    @Prop() placed: boolean = false;
    
    @Prop() eventId: string = "";
    @Prop() dataId: string;

	componentWillLoad() {
        const dataElement = document.getElementById(this.dataId);
        const data = JSON.parse(dataElement.innerHTML);

        if(this.checkout) DataLayer.order_checkout(data, this.eventId);
        else if(this.form) DataLayer.order_form(data, this.eventId);
        else if(this.placed) DataLayer.order_placed(data, this.eventId);
    }
}
